import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import main from '@/components/main'
import rigster from '@/components/rigster'
import topbar from '@/components/public/topbar'
import leftmenu from '@/components/menu/leftmenu'
import index from '@/components/public/index'
import users from '@/components/user/users'


Vue.use(Router)
var router= new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
      meta:{
        title:"登录"
      }
    },
    {
      path: '/rigster',
      name: 'rigster',
      component: rigster,
      meta:{
        title:"注册"
      }
    },
    {
      path: '/main',
      name: 'main',
      component:main,
      redirect:"/main/",
      children: [{
        path:"/main/",
        name:"default",
        component:main,
        meta: {
          // 添加该字段，表示进入这个路由是需要登录的
           requireAuth: true,  
          }, 
      },{
        path:"/main/:stateid",
        name:"index", 
        component:main,
        meta: {
          // 添加该字段，表示进入这个路由是需要登录的
           requireAuth: true,  
          },
      },
    ]
    },
    {
      path:"/userindex",
      name:"userindex",
      component:users,
      meta: {
        // 添加该字段，表示进入这个路由是需要登录的
         requireAuth: true,  
        },
    }
  ]
})

export default router;



