<template>
   <div>
      <div v-if="stateId" class="col-sm-12">
             <defaultPanel  :stateId="stateId" :statestitle="statesTitle"></defaultPanel>
      </div>
   </div>
</template>
<script>
import defaultPanel from "@/components/public/table";
export default {
  name: "index",
  components:{
  defaultPanel
  },
  data: function() {
    return {
      stateId: "",
      childId: "",
      childMenus: [],
      statesTitle: ""
    };
  },
  mounted: function() {
    this.loginhref();
  },
  methods: {
    //转为主页
    loginhref() {
      var vm = this;
      if (vm.stateId == "") {
        vm.$router.push("/main");
      }
    },
    //初始化href数据
    stateUpdateWatch(mainID) {
      var vm = this;
      if (mainID != "") {
        vm.stateId = mainID;
      } else {
        vm.stateId = "";
      }
      switch (vm.stateId) {
        case "jueseguanli":
          vm.statesTitle = "角色";
          break;
        case "gongnengguanli":
          vm.statesTitle = "功能";
          break;
        case "yonghuguanli":
          vm.statesTitle = "用户";
          break;
      }
    }
  },
  watch: {
    $route(to, from) {
      //   // 对路由变化作出响应...
      this.stateUpdateWatch(to.params.stateid);
      this.$validator.errors.clear(); //清除表单错误
    },
    componentId(to, from) {
      this.childId = to;
    }
  }
};
</script>
<style scoped>
.active a {
  background: #ccc !important;
}
</style>





